@import "../../../styles/mixins.scss";
@import "../../../styles/constants.scss";

.game-info__smile {
  @include border($color-light-border, $color-dark-border);
  width: 5.2rem;
  height: 5.36rem;
  @include flex(center, center);
  cursor: pointer;
  &:active {
    @include border($color-dark-border, $color-light-border);
  }
}

.game-info__image {
  font-size: 3.2rem;
}
